<template>
    <div id="proj">
        <ul>
            <li v-for='(proj,index) in projs' :key='index'>
                <h1>{{proj.title}}</h1>
                <span>{{proj.createTime}}</span>
                <p>{{proj.descs}}</p>
                <div>
                    <a :href="proj.showSrc">在线演示</a>
                    <a :href="proj.codeSrc">项目源码</a>
                    <a :href="proj.blogSrc">阅读全文</a>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
  name: "Project",
  data() {
    return {
      projs:[],
    };
  },
  methods: {},
  mounted() {
      this.$store.dispatch('changeHeadLine','项目');
      this.$api.getProjectListNoAuth()
      .then(({data:{code,projsLists}})=>{
        if(code==200){
          this.projs = projsLists
        }
      }).catch(err=>{
        alert(err.message);
      })
  }
};
</script>

<style lang="less" scoped>
#proj {
  margin: 50px auto;
  font-size: 1.8rem;
  ul {
    display: flex;
    padding: 0px;
    flex-direction: column;
    text-align: center;
    li {
      background-color: #fff;
      padding:0px 10px;
      margin-bottom: 20px;
      border: 1px solid #dcdcdc;
      h1 {
          color: #303030;
          font-weight: normal;
          font-size: 2rem;
      }
      span {
        color: #ccc;
        font-size: 1.5rem;
      }
      p {
        font-size: 1.2rem;
        text-align: left;
        text-indent: 2em;
      }
      a {
        font-size: 14px;
      }
    }
  }
}
</style>




